import { collection, getDocs, doc, setDoc, deleteDoc, query, where } from 'firebase/firestore';
import { db } from './firebase';
import { Film, Seance, Vote, User } from '../types';

export interface DatabaseBackup {
  films: Film[];
  seances: Seance[];
  votes: Vote[];
  users: User[];
  timestamp: Date;
  version: string;
}

/**
 * Créer une sauvegarde complète de la base de données
 */
export const createBackup = async (): Promise<DatabaseBackup> => {
  try {
    console.log('Début de la sauvegarde...');
    
    // Récupérer toutes les données
    const [filmsSnapshot, seancesSnapshot, votesSnapshot, usersSnapshot] = await Promise.all([
      getDocs(collection(db, 'films')),
      getDocs(collection(db, 'seances')),
      getDocs(collection(db, 'votes')),
      getDocs(collection(db, 'users'))
    ]);

    const films: Film[] = [];
    const seances: Seance[] = [];
    const votes: Vote[] = [];
    const users: User[] = [];

    // Traiter les films
    filmsSnapshot.forEach((doc) => {
      const data = doc.data();
      films.push({
        ...data,
        id: doc.id,
        createdAt: data.createdAt?.toDate() || new Date(),
        updatedAt: data.updatedAt?.toDate() || new Date(),
      } as Film);
    });

    // Traiter les séances
    seancesSnapshot.forEach((doc) => {
      const data = doc.data();
      seances.push({
        ...data,
        id: doc.id,
        date: data.date?.toDate() || new Date(),
        createdAt: data.createdAt?.toDate() || new Date(),
        updatedAt: data.updatedAt?.toDate() || new Date(),
      } as Seance);
    });

    // Traiter les votes
    votesSnapshot.forEach((doc) => {
      const data = doc.data();
      votes.push({
        ...data,
        id: doc.id,
        createdAt: data.createdAt?.toDate() || new Date(),
      } as Vote);
    });

    // Traiter les utilisateurs
    usersSnapshot.forEach((doc) => {
      const data = doc.data();
      users.push({
        uid: data.uid || doc.id,
        email: data.email || '',
        isAdmin: data.isAdmin || false,
        id: doc.id,
        createdAt: data.createdAt?.toDate() || new Date(),
        lastLogin: data.lastLogin?.toDate(),
      } as User);
    });

    const backup: DatabaseBackup = {
      films,
      seances,
      votes,
      users,
      timestamp: new Date(),
      version: '1.0.0'
    };

    console.log(`Sauvegarde créée : ${films.length} films, ${seances.length} séances, ${votes.length} votes, ${users.length} utilisateurs`);
    
    return backup;
  } catch (error) {
    console.error('Erreur lors de la création de la sauvegarde:', error);
    throw new Error('Impossible de créer la sauvegarde');
  }
};

/**
 * Télécharger une sauvegarde
 */
export const downloadBackup = async (backup: DatabaseBackup): Promise<void> => {
  try {
    const backupData = JSON.stringify(backup, null, 2);
    const blob = new Blob([backupData], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    
    const link = document.createElement('a');
    link.href = url;
    link.download = `backup-${backup.timestamp.toISOString().split('T')[0]}.json`;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    
    URL.revokeObjectURL(url);
  } catch (error) {
    console.error('Erreur lors du téléchargement de la sauvegarde:', error);
    throw new Error('Impossible de télécharger la sauvegarde');
  }
};

/**
 * Restaurer une sauvegarde
 */
export const restoreBackup = async (backup: DatabaseBackup): Promise<void> => {
  try {
    console.log('Début de la restauration...');
    
    // Supprimer toutes les données existantes
    await clearDatabase();
    
    // Restaurer les films
    for (const film of backup.films) {
      const { id, ...filmData } = film;
      await setDoc(doc(db, 'films', id), filmData);
    }
    
    // Restaurer les séances
    for (const seance of backup.seances) {
      const { id, ...seanceData } = seance;
      await setDoc(doc(db, 'seances', id), seanceData);
    }
    
    // Restaurer les votes
    for (const vote of backup.votes) {
      const { id, ...voteData } = vote;
      await setDoc(doc(db, 'votes', id), voteData);
    }
    
    // Restaurer les utilisateurs
    for (const user of backup.users) {
      const { uid, ...userData } = user;
      await setDoc(doc(db, 'users', uid), userData);
    }
    
    console.log('Restauration terminée avec succès');
  } catch (error) {
    console.error('Erreur lors de la restauration:', error);
    throw new Error('Impossible de restaurer la sauvegarde');
  }
};

/**
 * Nettoyer la base de données
 */
export const cleanupDatabase = async (): Promise<{ deletedVotes: number; deletedUsers: number }> => {
  try {
    console.log('Début du nettoyage...');
    
    let deletedVotes = 0;
    let deletedUsers = 0;
    
    // Supprimer les votes anonymes de plus de 30 jours
    const thirtyDaysAgo = new Date();
    thirtyDaysAgo.setDate(thirtyDaysAgo.getDate() - 30);
    
    const oldVotesQuery = query(
      collection(db, 'votes'),
      where('userId', '==', null),
      where('createdAt', '<', thirtyDaysAgo)
    );
    
    const oldVotesSnapshot = await getDocs(oldVotesQuery);
    const deleteVotePromises = oldVotesSnapshot.docs.map(async (doc) => {
      await deleteDoc(doc.ref);
      deletedVotes++;
    });
    
    await Promise.all(deleteVotePromises);
    
    // Supprimer les utilisateurs inactifs de plus de 90 jours
    const ninetyDaysAgo = new Date();
    ninetyDaysAgo.setDate(ninetyDaysAgo.getDate() - 90);
    
    const inactiveUsersQuery = query(
      collection(db, 'users'),
      where('lastLogin', '<', ninetyDaysAgo),
      where('isActive', '==', false)
    );
    
    const inactiveUsersSnapshot = await getDocs(inactiveUsersQuery);
    const deleteUserPromises = inactiveUsersSnapshot.docs.map(async (doc) => {
      await deleteDoc(doc.ref);
      deletedUsers++;
    });
    
    await Promise.all(deleteUserPromises);
    
    console.log(`Nettoyage terminé : ${deletedVotes} votes supprimés, ${deletedUsers} utilisateurs supprimés`);
    
    return { deletedVotes, deletedUsers };
  } catch (error) {
    console.error('Erreur lors du nettoyage:', error);
    throw new Error('Impossible de nettoyer la base de données');
  }
};

/**
 * Vider complètement la base de données
 */
const clearDatabase = async (): Promise<void> => {
  try {
    const collections = ['films', 'seances', 'votes', 'users'];
    
    for (const collectionName of collections) {
      const snapshot = await getDocs(collection(db, collectionName));
      const deletePromises = snapshot.docs.map(doc => deleteDoc(doc.ref));
      await Promise.all(deletePromises);
    }
  } catch (error) {
    console.error('Erreur lors du vidage de la base de données:', error);
    throw new Error('Impossible de vider la base de données');
  }
};

/**
 * Obtenir les statistiques de la base de données
 */
export const getDatabaseStats = async (): Promise<{
  totalFilms: number;
  totalSeances: number;
  totalVotes: number;
  totalUsers: number;
  databaseSize: string;
}> => {
  try {
    const [filmsSnapshot, seancesSnapshot, votesSnapshot, usersSnapshot] = await Promise.all([
      getDocs(collection(db, 'films')),
      getDocs(collection(db, 'seances')),
      getDocs(collection(db, 'votes')),
      getDocs(collection(db, 'users'))
    ]);

    const totalFilms = filmsSnapshot.size;
    const totalSeances = seancesSnapshot.size;
    const totalVotes = votesSnapshot.size;
    const totalUsers = usersSnapshot.size;
    
    // Estimation de la taille (approximative)
    const estimatedSize = (totalFilms + totalSeances + totalVotes + totalUsers) * 1024; // ~1KB par document
    const databaseSize = estimatedSize > 1024 * 1024 
      ? `${(estimatedSize / (1024 * 1024)).toFixed(1)} MB`
      : `${(estimatedSize / 1024).toFixed(1)} KB`;

    return {
      totalFilms,
      totalSeances,
      totalVotes,
      totalUsers,
      databaseSize
    };
  } catch (error) {
    console.error('Erreur lors de la récupération des statistiques:', error);
    throw new Error('Impossible de récupérer les statistiques');
  }
}; 